body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root{
  --black: rgb(20, 20, 20);
  --black-transparent:rgb(20, 20, 20,  0.70);
  --white: rgb(240,240,240);
  --blue: rgb(28, 145, 207);
  --red: rgb(221, 49, 49);
  --purple: hsl(264, 91%, 55%);
  --purplebacgro: hsl(265, 100%, 63%);
  --yellow: rgb(247, 224, 24);
}

body{
  background-color: rgb(20, 20, 20);
  background-color: var(--black);
  color: rgb(240,240,240);
  color: var(--white) ;
}

.flexbox{
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  user-select: none;
}

.flexdirection{
  flex-direction: column;
}


/* ================ Buttons =============== */
.sectionbtn{
  width: 100%;
  height: 100vh;
  background-color: rgb(20, 20, 20);
  background-color: var(--black);
  flex-direction: column;
}

.btnclic{
  width: 250px;
  height: 250px;
  background-color: hsl(265, 100%, 63%);
  background-color: var(--purplebacgro);
}

.btnyellow{
  width: 80%;
  border: 0px;
  border-radius:2em;
  background-color: rgb(247, 224, 24);
  background-color: var(--yellow);   
  font-size: 1.2em;
  font-weight: 600;
  color: hsl(264, 91%, 55%);
  color: var(--purple);
}


.logorounded{
  border-radius: 50%;
  border: 0px;
}

.circularbarprogress{
  width: 200px;
  height: 200px;
  margin-bottom: 3em;
}

/* .CircularProgressbar-background {
  fill: green;
} */
/* =================== DISPLAY GAME ============ */
.displaybtn{
  width: 300px;
  height: 300px;
  border: 5px solid rgb(240,240,240);
  border: 5px solid var(--white);
  border-radius: 1em;
}

.counterdisplay{
  width: 150px;
  height: 50px;
  text-align: center;
  margin-bottom: 1em;
  background-color: hsl(264, 91%, 55%);
  background-color: var(--purple);
}

.times{
  text-align: center;
  font-size: 1em;
}

.number{
  font-size: 2em;
}

/* =============== Display Inicio ============= */

.inicio{
  width: 100%;
  /* height: 100vh; */
  /* margin: 2em; */
  background-color: hsl(264, 91%, 55%);
  background-color: var(--purple);
}

.menuinicio{
  width: 90%;
  /* border: 1px solid red; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: 100vh;
  padding-bottom: 1em;
  padding-top: 1em;
}

.menuinicio.activate{
  position: absolute;
  top: -5000%;
}

.reglas-container{
  margin-right: auto;
  width:90%;
}

.txt-steps{
  color: rgb(247, 224, 24);
  color: var(--yellow);
  font-size: 3.5em;
  margin-right: 0.2em;
}
.form{
  width: 90%;
  height: auto;
  margin-bottom: 1em;
}

.input-name{
  width: 200px;
  background-color: transparent;
  border: 2px solid rgb(240,240,240);
  border: 2px solid var(--white);
  border-radius: 1em;
  padding: 1em;
  font-size: 1em;
  text-align: center;
  color: rgb(240,240,240);
  color: var(--white);
}


@media (min-width:359px){
    .reglas-container{
      width: 87%;
      margin-right: auto;
    }
    .input-name{
      width: 220px;
    }

    .btnyellow{
      width: 90%;
    }
}

@media (min-width:700px){

  .btnyellow{
    width: 50%;
  }
  .btnyellow:hover{
    background-color: hsl(264, 91%, 55%);
    background-color: var(--purple);
    color: rgb(247, 224, 24);
    color: var(--yellow);
    border: 1px solid rgb(247, 224, 24);
    border: 1px solid var(--yellow);
  }
}

.section-results{
    width: 100%;
    height: 100vh;
    position: absolute;
    background-color: var(--black-transparent);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}

.sect {
    width: 80%;
    height: auto;
    text-align: center;
    border-radius: 1em; 
    padding: 0.5em;
    background-color: var(--purple);
    /* backdrop-filter: blur(10px); */
}

.sect > h3{
    font-size: 1.5em;
}

.container__resultadosp{
    width: 70%;
    border: 3px solid var(--black);
    /* padding: 1em; */
    border-radius: 1em;
    margin-bottom: 1em;
}

.containerBtns{
   width: 100%;
   margin-top: 0.5em;
   margin-bottom: 0.5em;
}

span, .leyenda{
 font-weight: 700;
}

.txt-yellow{
    color: var(--yellow);
    font-size: 2em;
}

@media (min-width:812px) {
    .container__resultadosp{
        width: 50%;
    }
    .sect{
        width: 50%;
    }
}
